* {
  padding: 0;
  margin: 0;
  box-sizing: 0;
}

.cards {
  display: flex;
  margin: auto;
  width: 50%;
  margin-top: 80px;
}

.card {
  margin: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

/* Opacity #1 */
.hover11 img {
  opacity: 1;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.hover11 img:hover {
  opacity: 0.3;
}
